// Import dependencies
import * as faceapi from '@vladmandic/face-api';
import React, { useRef, useState, useEffect } from "react";
import * as tf from "@tensorflow/tfjs";
import Webcam from "react-webcam";
import "./App.css";
import { nextFrame } from "@tensorflow/tfjs";
// 2. TODO - Import drawing utility here
import {drawRect} from "./utilities"; 

function App() {
  const webcamRef = useRef(null);
    const canvasRef = useRef(null);
    var dataURI;
    var intervId;
    var coord1;
    var coord2;
    var width;
    var height;
    var isDetected = false;
    var webcam = new Image();
    var imgData;
    var sdrogo;
    var gg = "gg";
    var videoW;
    var videoH;
    var i;
    
  // Main function
    const runCoco = async () => {
        // 3. TODO - Load network 
        const net = await tf.loadGraphModel('https://testtfodausa.s3.eu-de.cloud-object-storage.appdomain.cloud/model.json');

        Promise.all([
            faceapi.nets.ssdMobilenetv1.loadFromUri("https://drive.google.com/drive/folders/1sGGNAfFXhonjsarvCcGmYH8Z0QLWPG-h?usp=sharing"),
                faceapi.nets.faceLandmark68Net.loadFromUri("https://drive.google.com/drive/folders/1sGGNAfFXhonjsarvCcGmYH8Z0QLWPG-h?usp=sharing"),
                faceapi.nets.faceRecognitionNet.loadFromUri("https://drive.google.com/drive/folders/1sGGNAfFXhonjsarvCcGmYH8Z0QLWPG-h?usp=sharing"),
        ]);

        function getLabeledFaceDescriptions() {
            const labels = ["Elon Musk", "Marco", "Obama"];
            return Promise.all(
                labels.map(async (label) => {

                    var descriptions = [];

                    for (i = 1; i <= 8; i++) {
                        const image = await faceapi.fetchImage(`./labels/${label}/${i}.jpeg`);

                        const detections = await faceapi
                            .detectSingleFace(image)
                            .withFaceLandMarks()
                            .withFaceDescriptor();

                        descriptions.push(detections.descriptor);
                    }
                    return new faceapi.LabeledFaceDescriptors(label, descriptions)
                })
            );
        }

        async function faceRecognition() {
            const labaledFaceDescriptors = await getLabeledFaceDescriptions();
            const faceMatcher = new faceapi.FaceMatcher(labaledFaceDescriptors);

            const canvas = faceapi.createCanvasFromMedia(document.getElementById("screenshot"));
            document.body.append(canvas);

            const displaySize = { width: 640, height: 480 };
            faceapi.matchDimensions(canvas, displaySize);

            setInterval(async () => {
                const detections = await faceapi
                    .detectAllFaces(document.getElementById("screenshot"))
                    .withFaceLandmarks()
                    .withFaceDescriptors();

                const resizedDetections = faceapi.resizeResults(detections, displaySize);

                canvas.getContext("2d").clearRect(0, 0, 640, 480);

                const results = resizedDetections.map((d) => {
                    return faceMatcher.findBestMatch(d.descriptor);
                });

                results.forEach((results, i) => {
                    const box = resizedDetections[i].detection.box;
                    const drawBox = new faceapi.draw.DrawBox(box, { label: results });
                    drawBox.draw(canvas)
                })

            }, 100);

        }

        // Loop and detect hands
        setInterval(() => {
            detect(net);
        }, 16.7);


        if (!intervId) {
            console.log(intervId);
            intervId = setInterval(() => {
                updateImageSource();
            }, 5000);
            console.log(intervId);
        }


        function updateImageSource() {
            if (isDetected && sdrogo) {
                var c = document.createElement('canvas');
                c.width = 640;
                c.height = 480;
                console.log("49", sdrogo);
                var ct = c.getContext('2d');
                var imm = new Image(640, 480);
                imm.src = sdrogo;
                imm.onload = function () {
                    console.log(imm);
                    ct.drawImage(imm, coord1 * 640, coord2 * 480, width * 640, height * 480, 0, 0, width * 640, height * 480);
                    console.log("funzione", sdrogo, coord1, coord2, width, height);
                    console.log("funzione", c);
                    dataURI = c.toDataURL('image/png');
                    console.log(dataURI);
                    document.getElementById("screenshot").src = dataURI;
                    isDetected = false;
                    faceRecognition();
                };
            }
        }
    };

  

  

  const detect = async (net) => {
    // Check data is available
     if (
       typeof webcamRef.current !== "undefined" &&
       webcamRef.current !== null &&
       webcamRef.current.video.readyState === 4
     ) {
       // Get Video Properties
       const video = webcamRef.current.video;
       const videoWidth = webcamRef.current.video.videoWidth;
       const videoHeight = webcamRef.current.video.videoHeight;

       // Set video width
       webcamRef.current.video.width = videoWidth;
       webcamRef.current.video.height = videoHeight;

       // Set canvas height and width
       canvasRef.current.width = videoWidth;
       canvasRef.current.height = videoHeight;

       // 4. TODO - Make Detections
       const img = tf.browser.fromPixels(video)
       const resized = tf.image.resizeBilinear(img, [640, 480])
       const casted = resized.cast('int32')
       const expanded = casted.expandDims(0)
       const obj = await net.executeAsync(expanded)

       

       const boxes = await obj[1].array()
       const classes = await obj[0].array()
       const scores = await obj[4].array()

       // Draw mesh
       const ctx = canvasRef.current.getContext("2d");
        

       // 5. TODO - Update drawing utility
       // drawSomething(obj, ctx) 
         console.log(scores[0][0]);
         console.log(sdrogo);
         if (scores[0][0] > 0.7) {
             //var webcam = new Image();
             sdrogo = await webcamRef.current.getScreenshot();
             videoW = videoWidth;
             console.log(boxes[0][0]);
             isDetected = true;
             console.log("108", sdrogo);
             coord1 = boxes[0][0][1];
             coord2 = boxes[0][0][0];
             width = boxes[0][0][3] - boxes[0][0][1];
             height = boxes[0][0][2] - boxes[0][0][0];
             //console.log("sdrogo", coord1, coord2, width, height);
             //console.log(sdrogo);
             //console.log("test");
             /* var pazzo = document.createElement('canvas');
             pazzo.width = 640;
             pazzo.height = 480;
             webcam = pazzo.getContext('2d');
             webcam = webcam.drawImage(video, 640, 480);
             */
             console.log("webcam aggiornata");
         }
       requestAnimationFrame(()=>{drawRect(boxes[0], classes[0], scores[0], 0.70, videoWidth, videoHeight, ctx)}); 
         

         

         

       tf.dispose(img)
       tf.dispose(resized)
       tf.dispose(casted)
       tf.dispose(expanded)
       tf.dispose(obj)

      }
      return sdrogo;
  };

  useEffect(() => {runCoco()},[]);

  return (
    <div className="App">
      <header className="App-header">
        <Webcam
          ref={webcamRef}
          id="webcam"
          muted={true} 
          style={{
            position: "absolute",
            marginLeft: "auto",
            marginRight: "auto",
            left: 0,
            right: 0,
            textAlign: "center",
            zindex: 9,
            width: 640,
            height: 480,
          }}
        />

        <canvas
          ref={canvasRef}
          style={{
            position: "absolute",
            marginLeft: "auto",
            marginRight: "auto",
            left: 0,
            right: 0,
            textAlign: "center",
            zindex: 8,
            width: 640,
            height: 480,
          }}
        />

              
                  <img
                      
                      id="screenshot"
                      alt="Screenshot"
                      style={{
                          width: 640,
                          height: 480,
                      }}
                      />
      </header>
    </div>
  );
}

export default App;
